import React, { useState, useEffect } from 'react';
import banksData from './banksData'; // Tp cha d liu ngn hng
import { VietQR } from 'vietqr';
import './App.css';

function QRCodeForm() {
  const [selectedBank, setSelectedBank] = useState('');
  const [bankDetails, setBankDetails] = useState({ BANK: '', STK: '', CTK: '' });
  const [amount, setAmount] = useState('');
  const [descriptionPrefix, setDescriptionPrefix] = useState('');
  const [descriptionSuffix] = useState(Date.now());
  const [statusMessage, setStatusMessage] = useState('');
  const [qrSrc, setQrSrc] = useState('');
  const [showImage, setShowImage] = useState(false);
  const [vietQRBanks, setVietQRBanks] = useState([]);
  const [selectedVietQRBank, setSelectedVietQRBank] = useState('');

  // Khi to VietQR v ly danh sch ngn hng
  useEffect(() => {
    // S dng danh sch ngn hng c nh (ACB, VCB, VPBank)
    setVietQRBanks([
      { bin: '970436', shortName: 'VCB', name: 'Ngn hng TMCP Ngoi Thng Vit Nam' },
      { bin: '970416', shortName: 'ACB', name: 'Ngn hng TMCP  Chu' },
      { bin: '970432', shortName: 'VPBank', name: 'Ngn hng TMCP Vit Nam Thnh Vng' }
    ]);
  }, []);

  // Khi chn thng hiu
  const handleSelectChange = (e) => {
    const selectedLabel = e.target.value;
    setSelectedBank(selectedLabel);
    const bank = banksData.find(b => b.LABEL === selectedLabel);

    if (selectedLabel === 'Ty chnh') {
      setBankDetails({ BANK: '', STK: '', CTK: '' });
      setSelectedVietQRBank('');
    } else if (bank) {
      setBankDetails({ BANK: bank.BANK, STK: bank.STK, CTK: bank.CTK });
    }
  };

  // Khi chn ngn hng t VietQR (cho trng hp ty chnh)
  const handleVietQRBankChange = (e) => {
    const selectedBin = e.target.value;
    setSelectedVietQRBank(selectedBin);
    
    const selectedBank = vietQRBanks.find(bank => bank.bin === selectedBin);
    if (selectedBank) {
      setBankDetails({
        ...bankDetails,
        BANK: `${selectedBank.name} - ${selectedBank.shortName} - ${selectedBank.bin}`
      });
    }
  };

  //Khi to QR code
  const handleCreateQr = async () => {
    if (!bankDetails.STK || !amount) {
      setStatusMessage("Vui lng nhp  thng tin!");
      setShowImage(false);
      return;
    }

    // X l cho trng hp ty chnh
    let bankId;
    if (selectedBank === 'Ty chnh') {
      // Nu l ty chnh, cn nhp y  thng tin ngn hng
      if (!bankDetails.BANK || !bankDetails.CTK) {
        setStatusMessage("Vui lng nhp y  thng tin ngn hng!");
        setShowImage(false);
        return;
      }
      // Gi s nh dng: "Tn ngn hng - Tn vit tt - M ngn hng"
      // Nu ngi dng ch nhp tn, ta s dng mt m mc nh
      const bankParts = bankDetails.BANK.split(' - ');
      bankId = bankParts.length >= 3 ? bankParts[2] : selectedVietQRBank || 'VCB'; // Mc nh VCB nu khng c m
    } else {
      bankId = bankDetails.BANK.split(' - ')[2];
    }

    const srcTemplate = `https://img.vietqr.io/image/${bankId}-${bankDetails.STK}-print.png?amount=${amount}&addInfo=${descriptionPrefix} ${descriptionSuffix}&accountName=${bankDetails.CTK}`;

    setQrSrc(srcTemplate);
    setShowImage(true);

    const imageQR = new Image();
    imageQR.crossOrigin = "anonymous";
    imageQR.src = srcTemplate;

    imageQR.onload = async function () {
      try {
        // Lu kch thc ban u
        imageQR.style.display = "block";

        const canvas = document.createElement("canvas");

        const ratioZoom = 1000 / imageQR.width;
        canvas.width = 1000; //  rng canvas c nh 1k
        canvas.height = Math.round(imageQR.height * ratioZoom); //  chieu cao canvas = hnh imageQR.height * ratioZoom;

        const ctx = canvas.getContext("2d");
        ctx.drawImage(imageQR, 0, 0, canvas.width, canvas.height);

        // Tm logo v khung ca ngn hng trong d liu
        const foundBank = selectedBank === 'Ty chnh' ? null : banksData.find(b => b.STK === bankDetails.STK);
        if (foundBank) {
          const logoImg = new Image();
          const logoText = new Image();
          const logoFrame = new Image();

          [logoImg, logoText, logoFrame].forEach(img => { img.crossOrigin = "anonymous"; });

          // Kim tra v ti hnh nh logo, text, frame
          if (foundBank.LOGO_IMG) logoImg.src = foundBank.LOGO_IMG;
          if (foundBank.LOGO_TEXT) logoText.src = foundBank.LOGO_TEXT;
          if (foundBank.FRAME) logoFrame.src = foundBank.FRAME;

          const imagesToLoad = [logoImg, logoText, logoFrame].filter(img => img.src);
          let countLoad = 0;

          // i cho n khi tt c cc hnh nh c ti
          imagesToLoad.forEach(img => {
            img.onload = () => { countLoad++; };
          });

          while (countLoad < imagesToLoad.length) {
            await new Promise(resolve => setTimeout(resolve, 500));
          }

          // V logo text
          if (logoText.src) {
            let height = 190;
            let width = Math.round(logoText.width * height / logoText.height);
            if (width > 620) {
              width = 620; height = Math.round(logoText.height * width / logoText.width);
            }
            const x = Math.round((canvas.width - width) / 2);
            const y = Math.round((190 - height) / 2);
            ctx.fillStyle = "white"; ctx.fillRect(0, 0, 1000, 190);
            ctx.drawImage(logoText, x, y, width, height);   // V text vo canvas
          }

          // V logo nh
          if (logoImg.src && logoImg.width === logoImg.height && logoImg.width > 0) {
            ctx.fillStyle = "white"; ctx.fillRect(460, 460, 85, 85);
            ctx.drawImage(logoImg, 455, 455, 95, 95);
          }

          if (logoFrame.src) {
            ctx.drawImage(logoFrame, 0, 0, canvas.width, canvas.height);
          }

          // Chuyn canvas thnh nh
          canvas.toBlob(async (blob) => {
            // Kim tra nu ang s dng PC/laptop hay mobile (gm c iPhone/iPad)
            const isMobile = /Mobi|Android|iPhone|iPad|iPod/i.test(navigator.userAgent);

            // Nu l PC/laptop -> ch copy
            if (!isMobile) {
              try {
                if (navigator.clipboard && window.ClipboardItem) {
                  await navigator.clipboard.write([new ClipboardItem({ "image/png": blob })]);
                  setStatusMessage("QR  c copy !");
                } else {
                  throw new Error("Clipboard API khng c h tr.");
                }
              } catch (copyError) {
                setStatusMessage("Li copy QR: " + copyError.message);
              }
            }

            // Nu l mobile (bao gm iPhone/iPad) -> va copy va ti v
            if (isMobile || !navigator.clipboard) {
              const downloadLink = document.createElement('a');
              downloadLink.href = URL.createObjectURL(blob);
              downloadLink.download = 'qr_code.png'; // Tn file khi ti v
              downloadLink.click(); // Kch hot ti v my

              setStatusMessage("QR  c copy ! nh  c ti v.");
            }
          }, "image/png");
        } else {
          // Trng hp ty chnh - ch to QR n gin
          canvas.toBlob(async (blob) => {
            const isMobile = /Mobi|Android|iPhone|iPad|iPod/i.test(navigator.userAgent);

            if (!isMobile) {
              try {
                if (navigator.clipboard && window.ClipboardItem) {
                  await navigator.clipboard.write([new ClipboardItem({ "image/png": blob })]);
                  setStatusMessage("QR  c copy !");
                } else {
                  throw new Error("Clipboard API khng c h tr.");
                }
              } catch (copyError) {
                setStatusMessage("Li copy QR: " + copyError.message);
              }
            }

            if (isMobile || !navigator.clipboard) {
              const downloadLink = document.createElement('a');
              downloadLink.href = URL.createObjectURL(blob);
              downloadLink.download = 'qr_code.png';
              downloadLink.click();

              setStatusMessage("QR  c copy ! nh  c ti v.");
            }
          }, "image/png");
        }
      }
      catch (error) {
        setStatusMessage("Li copy QR: ");
      }
      finally {
        imageQR.style.display = "none";
      }







    };
  };
 

  return (
    <div className="container">
      <form id="qrForm">
        <h2>To m QR</h2>

        <div className="form-group">
          <label htmlFor="bank_select">Thng hiu</label>
          <select id="bank_select" value={selectedBank} onChange={handleSelectChange}>
            <option value="" disabled>Chn thng hiu</option>
            {banksData.map(bank => (
              <option key={bank.LABEL} value={bank.LABEL}>{bank.LABEL}</option>
            ))}
            <option value="Ty chnh">Ty chnh</option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="banks">Ngn hng</label>
          {selectedBank === 'Ty chnh' ? (
            <select 
              id="banks" 
              value={selectedVietQRBank} 
              onChange={handleVietQRBankChange}
            >
              <option value="" disabled>Chn ngn hng</option>
              {vietQRBanks.map(bank => (
                <option key={bank.bin} value={bank.bin}>
                  {bank.shortName} - {bank.name}
                </option>
              ))}
            </select>
          ) : (
            <input 
              type="text" 
              id="banks" 
              value={bankDetails.BANK} 
              disabled
            />
          )}
        </div>

        <div className="form-group">
          <label htmlFor="ACCOUNT_NO">S ti khon</label>
          <input 
            type="text" 
            id="ACCOUNT_NO" 
            value={bankDetails.STK} 
            disabled={selectedBank !== 'Ty chnh'}
            onChange={e => setBankDetails({...bankDetails, STK: e.target.value})}
            placeholder={selectedBank === 'Ty chnh' ? 'Nhp s ti khon...' : ''}
          />
        </div>

        <div className="form-group">
          <label htmlFor="ACCOUNT_NAME">Ch ti khon</label>
          <input 
            type="text" 
            id="ACCOUNT_NAME" 
            value={bankDetails.CTK} 
            disabled={selectedBank !== 'Ty chnh'}
            onChange={e => setBankDetails({...bankDetails, CTK: e.target.value})}
            placeholder={selectedBank === 'Ty chnh' ? 'Nhp tn ch ti khon...' : ''}
          />
        </div>

        <div className="form-group">
          <label htmlFor="AMOUNT">S tin</label>
          <input type="text" id="AMOUNT" placeholder="Nhp s tin..." value={amount} onChange={e => setAmount(e.target.value)} />
        </div>

        <div className="form-group">
          <label htmlFor="DESCRIPTION">Ni dung</label>
          <div className="input-container">
            <input type="text" id="DESCRIPTION_PREFIX" value={descriptionPrefix} onChange={e => setDescriptionPrefix(e.target.value)} />
            <input type="text" id="DESCRIPTION_SUFFIX" value={descriptionSuffix} disabled />
          </div>
        </div>

        <button type="button" className="btn btn-success" onClick={handleCreateQr}>To m QR</button>

        {statusMessage && <p id="status">{statusMessage}</p>}
      </form>

      {showImage && (
        <div>
          <img id="imageQR" src={qrSrc} alt="QR Code" width="1000px" style={{ display: 'block' }} />
        </div>
      )}
    </div>
  );
}

export default QRCodeForm;
